/**
 *      
 */
export default class ApiService {
  /**
   * @param {string} baseUrl  
   * @param {string} authorization  
   */
  constructor(baseUrl, authorization) {
    this._baseUrl = baseUrl;
    this._authorization = authorization;
  }

  /**
   *      
   * @param {Object} config   
   * @param {string} config.route   
   * @param {string} [config.method]  
   * @param {string} [config.body]  
   * @param {Headers} [config.headers]  
   * @returns {Promise<Response>}
   */
  async _load({ route, method = 'GET', body = null, headers = new Headers() }) {
    headers.append('Authorization', this._authorization);

    const response = await fetch(`${this._baseUrl}${route}`, { method, body, headers });

    try {
      ApiService.checkStatus(response);
      return response;
    } catch (err) {
      ApiService.catchError(err);
    }
  }

  /**
   *    
   * @param {Response} response  
   * @returns {Promise}
   */
  static parseResponse(response) {
    return response.json();
  }

  /**
   *    
   * @param {Response} response  
   */
  static checkStatus(response) {
    if (!response.ok) {
      throw new Error(`${response.status}: ${response.statusText}`);
    }
  }

  /**
   *    
   * @param {Error} err  
   */
  static catchError(err) {
    throw err;
  }
}
